import { useState } from "react";

const Section = ({ heading, text, isActive, onToggle }) => {
  return (
    <div className="bg-white shadow-md rounded-lg p-6 mb-6">
      <h2 className="text-2xl font-semibold text-gray-800 mb-4">{heading}</h2>
      {isActive && <p className="text-gray-600">{text}</p>}
      <button onClick={() => onToggle()}>{isActive ? "Hide" : "Show"}</button>
    </div>
  );
};

const Instamart = () => {
  const [visibleSection, setVisibleSection] = useState("Careers");

  const toggleVisibleSection = (section) => {
    setVisibleSection(visibleSection === section ? "" : section);
  };

  return (
    <>
      <div className="bg-gray-100 p-8">
        <h1 className="text-4xl font-bold text-center text-gray-800 mb-10">
          Instamart
        </h1>
        <div className="max-w-screen-lg mx-auto">
          <Section
            isActive={visibleSection === "About"}
            onToggle={() => toggleVisibleSection("About")}
            heading="About Instamart"
            text="Lorem ipsum dolor sit amet..."
          />
          <Section
            isActive={visibleSection === "Team"}
            onToggle={() => toggleVisibleSection("Team")}
            heading="Team Instamart"
            text="Lorem ipsum dolor sit amet..."
          />
          <Section
            isActive={visibleSection === "Careers"}
            onToggle={() => toggleVisibleSection("Careers")}
            heading="Careers"
            text="Lorem ipsum dolor sit amet..."
          />
        </div>
      </div>
    </>
  );
};

export default Instamart;
